.stats-view{
    width: 50%;
    height: 100%;
    padding: 10px;
    background-color: #1d9ec2;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.sprite-image{
    width: 150px;
    height: 150px;
    margin: 10px auto;
    background-color: #a9d250;
    border: 15px solid #dedede;
    border-radius: 5px;
    color: #585858; 
}

.data-wrapper{
    width: 80%;
    height: 110px;
    padding: 10px;
    margin: 0 auto;
    background-color: #8e8181;
    border-radius: 5px;
    box-shadow: inset 0 0 20px rgba(0, 0, 0, 0.22);
    color: rgb(250, 251, 251);
    
}

.data-name{
    margin: 0 0 5px;
}

.data-char{
    font-size: 10px;
}